.parallax {
  /* The image used */
  background-image: url("./kevBanner.jpg");
  /* Set a specific height */
  min-height: 500px;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
}

.nav-item {
  font-size: 1.3vw;
}

@media (max-width: 768px) {
  .nav-item {
      font-size: 2vw;
  }
}

/*Font*/

body, input, select, textarea {
  font-family: "Raleway", Arial, Helvetica, sans-serif;
  font-size: 1em;
  font-weight: 400;
  line-height: 2em;
}

/* Makes the header translucent*/

#top-banner, .clear-section {
  background-color: rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
  /* RGB of 0,0,0 is black. */
}

/* Custom styling for the buttons */

.btn {
  text-transform: uppercase;
  font-family: "Raleway", Arial, Helvetica, sans-serif;
  font-size: 1em;
  font-weight: 400;
  line-height: 2em;
}

/* Returns main landmark to blank background */

main {
  background-color: transparent;
  padding-bottom: 0;
}

/* Heading Level 2 Style */

h2 {
  font-size: 2.5em;
  text-transform: uppercase;
}

/* Section One Style */

#one {
  color: #777;
  background-color: white;
  text-align: center;
  /* padding: 50px 80px; */
  text-align: justify;
  padding: 6em 0 4em 0
}

/* Styling for the Banner, mostly used from the template*/

.clear-section {
  padding: 10em 0 13em 0;
  line-height: 1.75;
  text-align: center;
}

.clear-section:last-child {
  margin-bottom: 0;
}

.clear-section h2 {
  color: #ffffff;
  display: inline-block;
  font-size: 3.5em;
  line-height: 1.35;
  margin-bottom: 0.5em;
}

.clear-section p {
  color: #aaa;
  font-size: 1.5em;
  margin-bottom: 1.75em;
  text-transform: uppercase;
}

@media screen and (max-width: 1280px) {
  .clear-section {
      padding: 7em 0 11em 0;
  }
  .clear-section h2 {
      font-size: 2.5em;
  }
  .clear-section p {
      font-size: 1.5em;
  }
}

@media screen and (max-width: 980px) {
  .clear-section {
      padding: 6em 0 7em 0;
  }
}

@media screen and (max-width: 736px) {
  .clear-section {
      padding: 5em 2em 6em 2em;
  }
  .clear-section br {
      display: none;
  }
  .clear-section h2 {
      font-size: 2.25em;
  }
  .clear-section p {
      font-size: 1em;
  }
}

@media screen and (max-width: 480px) {
  .clear-section h2 {
      font-size: 1em;
  }
  .clear-section p {
      font-size: 1em;
  }
  .clear-section {
      padding: 4em 2em 4em 2em;
  }
}

/*Spacing for the sub description */

#sub-description {
  text-transform: uppercase;
  font-size: 1.2em;
  padding-right: 2em;
}

@media (max-width: 978px) {
  #sub-description {
      padding-right: 0em;
  }
}

/*Font size for the heading in the bottom section*/

#two h2 {
  font-size: 2.5em;
}

/* Fixes padding in the bottom section */

#two {
  padding: 6em 0 6em 0;
}

/* Footer Styling, Mostly from the template */

#footer {
  margin-top: 0;
  padding: 4em 0 3em 0;
  background: #f6f6f6;
  color: #aaa;
  text-align: center;
}

#footer a {
  color: #aaa;
  text-decoration: none;
}

#footer a:active, #footer a:hover {
  color: #666;
}

#footer .icons {
  font-size: 1.5em;
}

#footer .icons li {
  padding-right: 2.5em;
}

#footer .icons li:last-child {
  padding-right: 0;
}

#footer .copyright {
  margin: 2em 0;
  padding: 0;
  text-align: center;
}

#footer .copyright li {
  border-left: solid 1px rgba(144, 144, 144, 0.25);
  display: inline-block;
  list-style: none;
  margin-left: 1.5em;
  padding-left: 1.5em;
}

#footer .copyright li:first-child {
  border-left: 0;
  margin-left: 0;
  padding-left: 0;
}

@media screen and (max-width: 980px) {
  #footer {
      padding: 3em 0 2em 0;
  }
}

@media screen and (max-width: 736px) {
  #footer {
      padding: 1.5em 0 1em 0;
  }
  #footer .icons {
      font-size: 1em;
  }
}

@media screen and (max-width: 480px) {
  #footer {
      padding: 1.5em 0 0.5em 0;
  }
  #footer .copyright li {
      border-left: 0;
      display: block;
      margin: 0;
      padding: 0;
  }
}

/* Icon */

.icon {
  text-decoration: none;
  border-bottom: none;
  position: relative;
}

.icon:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-transform: none !important;
}

.icon>.label {
  display: none;
}

ul.icons {
  cursor: default;
  list-style: none;
  padding-left: 0;
}

ul.icons li {
  display: inline-block;
  padding: 0 1em 0 0;
}

ul.icons li:last-child {
  padding-right: 0;
}

ul.icons li .icon:before {
  font-size: 2em;
}

/* Font size for the navbar */

.navbar-brand {
  font-size: 1.5em;
}